import styled from "styled-components";

export const ContactLi = styled.li`
  font-size: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 300px;
  border: 1px solid #fff;
  padding: 17px 0px;
  margin: 0px auto 10px;
  border-radius: 15px;
  color: #a19898;
`

export const ContactImg = styled.img`
  width: 75px;
  margin-bottom: 5px;
`