import React from "react";
import {ResumeWrapper, Wrap} from "./styledResume";
import Heading from "../UI/Heading/Heading";
import Education from "../Education/Education";
import WorkExperience from "../WorkExperience/WorkExperience";


const Resume = (props) => {
    return (
        <ResumeWrapper>
            <Heading title={props.title}/>
            <Wrap>
            <Education education={props.education} subtitle={props.subtitle}/>
            <WorkExperience work={props.work} subtitle={props.subtitle}/>
            </Wrap>
        </ResumeWrapper>
    )
}

export default Resume;