import React from 'react';
import {
    CardBox,
    CardDescription,
    CardImg,
    CardLink,
    CardName,
    CardTech,
    CardTechnologies,
    CardWrapper
} from "./styledCard";


const Card = (props) => {
    return (
        <CardBox>
            <CardWrapper>
                <CardName>{props.name}</CardName>
                <CardImg src={props.image}/>
                <CardDescription>{props.descriptions}</CardDescription>
                <CardTechnologies><CardTech>{props.tech}</CardTech>{props.technologies}</CardTechnologies>
                <CardLink href={props.link}>See more</CardLink>
            </CardWrapper>
        </CardBox>
    )
}

export default Card;