import React from "react";
import Navigation from "./Modules/Navigation/Navigation";
import {BrowserRouter, Route} from "react-router-dom";
import Profile from "./Modules/Profile/Profile";
import {Main, Wrapper} from "./styledApp";
import About from "./Modules/About/About";
import Resume from "./Modules/Resume/Resume";
import Contact from "./Modules/Contact/Contact";
import Portfolio from "./Modules/Portfolio/Portfolio";
import Home from "./Modules/Home/Home";


function App(props) {

    let profilePage = props.localState._state.profilePage;
    let aboutPage = props.localState._state.aboutPage;
    let technologies = props.localState._state.technologies;
    let resumePage = props.localState._state.resumePage;
    let portfolioPage = props.localState._state.portfolioPage;
    let contactPage = props.localState._state.contactPage;

    return (
        <BrowserRouter>
            <div className="App">
                <Main>
                    <Navigation/>
                    <Profile name={profilePage.name} position={profilePage.position}/>
                    <Wrapper>
                        <Route path={'/home'} render={() => <Home/>}/>
                        <Route path={'/about'}
                               render={() => <About title={aboutPage.title} text={aboutPage.text} info={aboutPage.info}
                                                    techTitle={technologies.title}/>}/>
                        <Route path={'/resume'}
                               render={() => <Resume title={resumePage.title} education={resumePage.education}
                                                     work={resumePage.workExperience}
                                                     subtitle={resumePage.subtitles}/>}/>
                        <Route path={'/portfolio'}
                               render={() => <Portfolio title={portfolioPage.title} projects={portfolioPage.projects}/>}/>
                        <Route path={'/contact'}
                               render={() => <Contact title={contactPage.title} cards={contactPage.cards}
                                                      button={contactPage.button}/>}/>
                    </Wrapper>
                </Main>
            </div>
        </BrowserRouter>
    );
}

export default App;
