import React from "react";
import {ContactImg, ContactLi} from "./styledContactCards";
import done from '../../assets/done.svg';
import location from '../../assets/location.svg';
import phone from '../../assets/phone.svg';
import mail from '../../assets/letter.svg'


const ContactCards = (props) => {
    return (
        <ul>
            <ContactLi><ContactImg src={done}/>{props.cards.open}</ContactLi>
            <ContactLi><ContactImg src={location}/>{props.cards.location}</ContactLi>
            <ContactLi><ContactImg src={phone}/>{props.cards.phone}</ContactLi>
            <ContactLi><ContactImg src={mail}/>{props.cards.mail}</ContactLi>
        </ul>
    )
}

export default ContactCards;