import styled from "styled-components";

export const ResumeWrapper = styled.div`
`

export const Wrap = styled.div`
  display: grid;
  grid-template-columns: 6fr 6fr;
`

export const ResumeItem = styled.li`
  border: 1px solid #a19898;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  text-align: center;
  color: #fff;
  margin-bottom: 20px;
  border-radius: 15px;
  width: 380px;
  height: 170px;
`

export const ResumeSpan = styled.span`
  color: #a19898;
  font-size: 18px;
`

export const ResumeSpan2 = styled.span`
margin: 15px 0px;
`