import React from "react";
import {Info, InfoItem, Span} from "./styledPersonalInfo";


const PersonalInfo = (props) => {
    return (
        <Info>
            <InfoItem><Span>Age:</Span> {props.info.age}</InfoItem>
            <InfoItem><Span>Residence:</Span> {props.info.residence}</InfoItem>
            <InfoItem><Span>Location:</Span> {props.info.location}</InfoItem>
            <InfoItem><Span>Email:</Span> {props.info.mail}</InfoItem>
            <InfoItem><Span>Phone:</Span> {props.info.phone}</InfoItem>
        </Info>
    )
}

export default PersonalInfo;