import React from "react";
import {WorkExpWrapper} from "./styledWorkExperience";
import Subtitle from "../UI/Subtitle/Subtitle";
import {ResumeItem, ResumeSpan, ResumeSpan2} from "../Resume/styledResume";

const WorkExperience = (props) => {
    return (
        <WorkExpWrapper>
            <Subtitle subtitle={props.subtitle.exp}/>
            <ul>
                <ResumeItem><ResumeSpan>{props.work.vendino.years}</ResumeSpan><ResumeSpan2>{props.work.vendino.company}</ResumeSpan2><ResumeSpan>{props.work.vendino.position}</ResumeSpan></ResumeItem>
                <ResumeItem><ResumeSpan>{props.work.meatGuild.years}</ResumeSpan><ResumeSpan2>{props.work.meatGuild.company}</ResumeSpan2><ResumeSpan>{props.work.meatGuild.position}</ResumeSpan></ResumeItem>
                <ResumeItem><ResumeSpan>{props.work.foxtrot.years}</ResumeSpan><ResumeSpan2>{props.work.foxtrot.company}</ResumeSpan2><ResumeSpan>{props.work.foxtrot.position}</ResumeSpan></ResumeItem>
            </ul>
        </WorkExpWrapper>
    )
}

export default WorkExperience;