import React from "react";
import {NavLink} from "react-router-dom";
import home from '../../assets/home.svg';
import person from '../../assets/person.svg';
import educational from '../../assets/educational.svg';
import portfolio from '../../assets/portfolio.svg';
import contact from '../../assets/letter.svg'


import {Icon, Li, Nav} from "./styledNavigation";

const Navigation = () => {
    return (
        <Nav>
            <ul>
                <NavLink to='/home'>
                    <Li> <Icon src={home}/> Home</Li>
                </NavLink>
                <NavLink to={'/about'}>
                    <Li> <Icon src={person}/>About Me</Li>
                </NavLink>
                <NavLink to={'/resume'}>
                    <Li> <Icon src={educational}/>Resume</Li>
                </NavLink>
                <NavLink to={'/portfolio'}>
                    <Li> <Icon src={portfolio}/>Portfolio</Li>
                </NavLink>
                <NavLink to={'/contact'}>
                    <Li> <Icon src={contact}/>Contact</Li>
                </NavLink>
            </ul>
        </Nav>
    )
};

export default Navigation;