import React from 'react';
import {Bttn, BttnWrapper} from "./styledButton";
import CV from '../../../assets/Alex_Dordiuk_CV.pdf'

const Button = () => {
    return (
        <BttnWrapper>
            <Bttn><a href={CV} download>Download CV</a></Bttn>
        </BttnWrapper>
    )
};

export default Button;