import styled from "styled-components";


export const BttnWrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
`

export const Bttn = styled.button`
  padding: 10px 20px;
  font-size: 25px;
  border-radius: 25px;
  outline: none;
  border: 3px solid transparent;
  font-weight: 600;
  cursor: pointer;
  
  &:hover {
    color: #23bcad;
    border-color: #23bcad;
    background-color: #272020;
  }
`