import React from "react";
import {EducationWrapper} from "./styledEducation";
import Subtitle from "../UI/Subtitle/Subtitle";
import {ResumeItem, ResumeSpan, ResumeSpan2} from "../Resume/styledResume";


const Education = (props) => {
    return (
        <EducationWrapper>
            <Subtitle subtitle={props.subtitle.edu}/>
            <ul>
                <ResumeItem><ResumeSpan>{props.education.beetroot.years}</ResumeSpan>
                    <ResumeSpan2>{props.education.beetroot.college}</ResumeSpan2>
                    <ResumeSpan>{props.education.beetroot.degree}</ResumeSpan></ResumeItem>
                <ResumeItem><ResumeSpan>{props.education.navs.years}</ResumeSpan>
                    <ResumeSpan2>{props.education.navs.college}</ResumeSpan2>
                    <ResumeSpan>{props.education.navs.degree}</ResumeSpan></ResumeItem>
                <ResumeItem><ResumeSpan>{props.education.zieit.years}</ResumeSpan>
                    <ResumeSpan2>{props.education.zieit.college}</ResumeSpan2>
                    <ResumeSpan>{props.education.zieit.degree}</ResumeSpan></ResumeItem>
            </ul>
        </EducationWrapper>
    )
}

export default Education;