import React from "react";
import {Logo, LogoDiv} from "./styledTechLogos";
import html from '../../assets/html.svg';
import css from '../../assets/css.svg';
import sass from '../../assets/sass.svg';
import js from '../../assets/javascript.svg';
import jq from '../../assets/jquery.svg';
import react from '../../assets/react.svg';
import xd from '../../assets/xd.svg';
import figma from '../../assets/figma.svg';
import github from '../../assets/github.svg';
import google from '../../assets/google.svg'


const TechLogos = () => {
    return (
        <LogoDiv>
            <Logo src={html} alt='HTML-5'/>
            <Logo src={css} alt='CSS-3'/>
            <Logo src={sass} alt='SASS'/>
            <Logo src={js} alt='JavaScript'/>
            <Logo src={jq} alt='Jquery'/>
            <Logo src={react} alt='React'/>
            <Logo src={xd} alt='Adobe XD'/>
            <Logo src={figma} alt='Figma'/>
            <Logo src={github} alt='Github'/>
            <Logo src={google} alt='Google'/>
        </LogoDiv>
    )
}

export default TechLogos;