import React from "react";
import {Bttn, Form, FormSpan, FormTitle, Input, TextArea} from "./styledContactForm";
import emailjs from 'emailjs-com'


const ContactForm = (props) => {

    function sendEmail(e) {
        e.preventDefault();

        emailjs.sendForm('service_0nvwi6l', 'template_q9o7pfh', e.target, 'user_a6i5tGA74zdPZ4uvcxOiy')
            .then((result) => {
                console.log(result.text);
            }, (error) => {
                console.log(error.text);
            });
        e.target.reset();
    }


    return (
        <Form onSubmit={sendEmail}>
            <FormTitle>How Can I <FormSpan>Help You?</FormSpan></FormTitle>
            <Input type={'text'} name={'name'} placeholder={'Write Your Name*'} required/>
            <Input type={'text'} name={'surname'} placeholder={'Write Your Surname'}/>
            <Input type={'email'} name={'email'} placeholder={'Write Your Email*'} required/>
            <TextArea name={'message'} placeholder={'Message*'} required/>
            <Bttn type={'submit'}>{props.button}</Bttn>
        </Form>
    )
}

export default ContactForm;