import React from 'react';
import Heading from "../UI/Heading/Heading";
import {PortfolioWrapper} from "./styledPortfolio";
import Card from "../UI/Card/Card";


const Portfolio = (props) => {

    let cardName = props.projects.map((item, index) =>  {
        return <Card name={item.name} descriptions={item.description} technologies={item.technologies} link={item.link} image={item.image} tech={'Technologies used:'}/>
    })

    return (
        <PortfolioWrapper>
            <Heading title={props.title}/>
            {cardName}
        </PortfolioWrapper>
    )
}


export default Portfolio;