import React from "react";
import {Icon, Link, SocialLinks} from "./styledSocials";
import telegram from '../../assets/telegram.svg';
import linkedIn from '../../assets/linkedin.svg';
import github from '../../assets/github.svg'

const Socials = () => {
    return (
        <SocialLinks>
            <Link href={'https://web.telegram.org/#/im?p=@Drawen77'}> <Icon src={telegram}/></Link>
            <Link href={'https://www.linkedin.com/in/aleksandrdordiuk/?locale=en_US'}> <Icon src={linkedIn}/></Link>
            <Link href={'https://github.com/Drawen77'}> <Icon src={github}/></Link>
        </SocialLinks>
    )
}

export default Socials;