import styled from "styled-components";

export const Form = styled.form`
  display: flex;
  flex-direction: column;
  padding: 0px 80px 0px 20px;
`

export const Input = styled.input`
  padding: 20px;
  margin-bottom: 20px;
  border-radius: 15px;
  outline: none;
  border: 5px solid transparent;
  
  
  &:active,
  &:focus {
    border-color: #23bcad;
    color: #23bcad;
  }
`

export const FormTitle = styled.h3`
  color: #fff;
  font-size: 30px;
  margin-bottom: 30px;
`

export  const FormSpan = styled.span`
  color: #23bcad;
`

export const TextArea = styled.textarea`
  padding: 20px 20px 120px;
  border-radius: 15px;
  outline: none;
  border: 5px solid transparent;
  margin-bottom: 20px;

  &:active,
  &:focus {
    border-color: #23bcad;
    color: #23bcad;
  }
  
`

export const Bttn = styled.button`
  padding: 10px 20px;
  font-size: 25px;
  border-radius: 25px;
  outline: none;
  border: 3px solid transparent;
  font-weight: 600;
  cursor: pointer;
  min-width: 250px;
  margin: 0 auto;
  
  &:hover {
    color: #23bcad;
    border-color: #23bcad;
    background-color: #272020;
  }
  
  
`