import React from "react";
import myPhoto from '../../assets/my_photo.jpg';
import {Name, Photo, PhotoWrapper, Position, ProfileWrapper, Rights, Wrap} from "./styledProfile";
import {SocialLinks} from "../Socials/styledSocials";
import Socials from "../Socials/Socials";
import Button from "../UI/Button/Button";


const Profile = (props) => {
    return (
        <ProfileWrapper>
            <Wrap>
                <PhotoWrapper>
                    <Photo src={myPhoto}/>
                </PhotoWrapper>
                <Name>{props.name}</Name>
                <Position>{props.position}</Position>
                <Socials/>
                <Button/>
            </Wrap>
            <Rights>© 2021 All rights reserved.</Rights>
        </ProfileWrapper>
    )
}

export default Profile;